import React from 'react';

import {
  NewPrice,
  OldPrice,
  SubPackageLength,
  SubPackageType,
  TakeThisOfferButton,
  Terms,
  Wrapper,
} from './SubscriptionOfferCard.styles';
import SubscriptionOfferCardProps from './SubscriptionOfferCard.types';

const SubscriptionOfferCard: React.FC<SubscriptionOfferCardProps> = ({
  subscriptionType = 'Independent Premium',
  subscriptionLength,
  newPrice,
  oldPrice,
  buttonProps = {},
  terms,
  className,
}) => (
  <Wrapper className={className} badgeText="subscription offer">
    <SubPackageType>{subscriptionType}</SubPackageType>
    <SubPackageLength>{subscriptionLength}</SubPackageLength>
    <NewPrice>{newPrice}</NewPrice>
    <OldPrice>{oldPrice}</OldPrice>
    <TakeThisOfferButton {...buttonProps}>Take this offer</TakeThisOfferButton>
    <Terms>{terms}</Terms>
  </Wrapper>
);

export default SubscriptionOfferCard;
